<template>
  <div id="box">
    <div class="orderDetails">
      <div class="head">
        <div class="title">{{order_goods.order_status_detail}}</div>
        <!--         <div class="text">卖家已发货，请准备收货</div> -->
      </div>
      <div class="logistics">
        <div class="item">
          <p class="title">物流信息： 顺丰快递 2548974512648</p>
          <p class="time">2019-03-02 19:14:17</p>
        </div>
      </div>
      <div class="wrap">
        <div class="msg">
          <h4 class="msg1">商品信息</h4>
          <div class="product flex">
            <img :src="order_goods.order_goods[0].original_img" />
            <div class="info flex_1">
              <p class="title">{{order_goods.order_goods[0].goods_name}}</p>
              <p class="price">
                ￥{{order_goods.order_goods[0].goods_price}}
                <span>X{{order_goods.order_goods[0].goods_num}}</span>
              </p>
            </div>
          </div>
        </div>
        <div class="list">
          <h4>收货地址</h4>
          <div class="item flex">
            <div class="title">收件人:</div>
            <div class="text">{{order_goods.consignee}}</div>
          </div>
          <div class="item flex">
            <div class="title">电话号码：</div>
            <div class="text">{{order_goods.mobile}}</div>
          </div>
          <div class="item flex">
            <div class="title">收货地址：</div>
            <div class="text">深圳市龙华新区民治街道牛栏前大厦B1215</div>
          </div>
        </div>
        <div class="list">
          <h4>订单信息</h4>
          <div class="item flex-between">
            <div class="title">订单号码：</div>
            <div class="text">{{order_goods.order_sn}}</div>
          </div>
          <div class="item flex-between">
            <div class="title">下单时间：</div>
            <div class="text">2019-11-30 10:42:38</div>
          </div>
          <div class="item flex-between">
            <div class="title">支付方式：</div>
            <div class="text">支付宝</div>
          </div>
          <div class="item flex-between">
            <div class="title">备注：</div>
            <div class="text" style="color: #868686">无</div>
          </div>
        </div>
        <div class="list">
          <div class="item flex-between">
            <div class="title">商品金额：</div>
            <div class="text">￥{{order_goods.total_amount}}</div>
          </div>
          <div class="item flex-between">
            <div class="title">运费：</div>
            <div class="text">+￥0.00</div>
          </div>
          <div class="item flex-between">
            <div class="title">优惠券抵扣：</div>
            <div class="text">-￥0.00</div>
          </div>
          <div class="item flex-between">
            <div class="title">积分抵扣：</div>
            <div class="text">-￥0.00</div>
          </div>
          <div class="item flex-between">
            <div class="title">实付金额：</div>
            <div class="text" style="color: #e22424">￥{{order_goods.total_amount}}0</div>
          </div>
        </div>
      </div>
      <div class="handle flex-center-y">
        <div class="border flex-center">联系客服</div>
        <!-- <div class="border flex-center">取消订单</div> -->
        <!-- <div class="background flex-center">付款</div> -->
        <div class="background_blue flex-center">确认收货</div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/axios/api.js";
import { NavBar } from "vant";
export default {
  data() {
    return {
      order_goods: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();

      params.append("id", this.$route.query.id);
      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.orderDetail, params).then(res => {
        this.order_goods = res.data.data;
      });
    }
  },
  components: {
    vanNavBar: NavBar
  }
};
</script>

<style scoped lang="scss">
.orderDetails {
  overflow: hidden;
}
.head {
  padding: 20px 10px;
  background: linear-gradient(
    45deg,
    rgba(247, 31, 64, 1),
    rgba(255, 102, 48, 1)
  );
  color: #fff;
  .title {
    font-size: 24px;
    margin-bottom: 14px;
  }
}
.logistics {
  padding: 10px 30px;
  box-shadow: 0 4px 20px #e6e6e6;
  background: #fff;
  border-radius: 14px;
  margin-bottom: 30px;
  width: 80%;
  margin: 0 auto;
  font-size: 13px;
  .item {
    .time {
      margin-top: 15px;
      color: #848484;
    }
    .address {
      margin-top: 15px;
      color: #4c4c4c;
    }
  }
}
.wrap {
  background: #fff;
  margin-bottom: 100px;
  padding: 0 10px;
  .lepin {
    margin-bottom: 50px;
    .inner {
      margin: 20px 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .button {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 150px;
        height: 60px;
        border-radius: 60px;
        border: 2px solid #eee;
        color: #848484;
      }
    }
  }
  .msg {
    .msg1 {
      font-size: 16px;
      font-weight: 100;
      color: #333;
    }
  }
  .product {
    display: flex;
    padding: 25px 0;
    border-bottom: 1px solid #eee;
    img {
      width: 60px;
      height: 60px;
      border-radius: 10px;
    }
    .info {
      overflow: hidden;
      padding-left: 20px;
      .title {
        font-size: 14px;
        margin-bottom: 8px;
      }
      span {
        color: #848484;
      }
      .afterSale {
        justify-content: flex-end;
        .button {
          width: 150px;
          height: 60px;
          border-radius: 60px;
          border: 2px solid #eee;
          color: #848484;
        }
      }
    }
  }
  .list {
    padding: 20px 0;
    border-bottom: 1px solid #eee;
    h4 {
      font-size: 16px;
      font-weight: 100;
      margin-bottom: 30px;
      color: #333;
    }
    .item {
      display: flex;
      margin-bottom: 20px;
      font-size: 13px;
      &:last-child {
        margin-bottom: 0;
      }
      .title {
        color: #868686;
      }
      .text {
        color: #020202;
        margin-left: auto;
      }
    }
  }
}
.handle {
  display: flex;
  align-items: center;
  height: 55px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 0 10px #ece8e8;
  justify-content: flex-end;
  div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85px;
    height: 27px;
    border-radius: 30px;
    margin-right: 20px;
    font-size: 13px;
  }
  .border {
    border: 2px solid #efefef;
    box-sizing: border-box;
    color: #545454;
  }
  .background {
    background: linear-gradient(to right, #fcd54e, #edb342);
    color: #fff;
  }
  .background_blue {
    background: linear-gradient(to right, #fd7253, #ef4821);
    color: #fff;
  }
}
</style>




